@import 'variables';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

body {
    display: flex;

    nav {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        background-color: $nav_bg_color;
        border-radius: 0px 31px 31px 0px;
        width: 10rem;
        height: 100vh;
        flex: 1.5;

        a {
            display: flex;
            font-size: 1.6rem;
            font-family: $text_font_family;
            justify-content: center;
            align-items: center;
            text-decoration: none;
            color: $nav_button_p_color;
            width: 15rem;
            height: 3.5rem;
            border-radius: 0.5rem;
            background-color: $button_bg_color;
            border: none;
        }
    }

    .content {
        display: flex;
        flex-direction: column;
        align-items: center;   
        justify-content: space-evenly;
        flex: 9;

        header {
            width: 100%;
            height: 8rem;
            display: flex;
            align-items: center;
            justify-content: center;
            font-family: $title_font_family;
            font-weight: 400;
    
            p {
                display: flex;
                align-items: center;
                font-size: 4.5rem;
                font-weight: 500;
                
                span {
                    font-weight: 600;
                    font-size: 7rem;
                }
            }
        }
    
       main {
            height: 40rem;
            width: 80%;
            margin: 2rem;
            display: flex;
            align-items: center;
            justify-content: space-between;

            .main_img {
                width: 30%;
                height: 100%;
                background-image: url(/images/David\ Chaum.jpg);
                background-repeat: no-repeat;
                background-position: center;
                border-radius: 1.5rem;
                box-shadow: $img_card_box_shadow;
            }

            .main_txt {
                height: 40rem;
                width: 60rem;
                background-color: $card_bg_color;
                border-radius: 1.5rem;
                box-shadow: $img_card_box_shadow;
                display: flex;
                flex-direction: column;
                align-items: center;
                
                h2 {
                    font-size: 3rem;
                    color: $card_title_color;
                    font-family: $title_font_family;
                }

                a {
                    color: $card_p_color;
                }

                p {
                    font-family: $card_text_font_family;
                    font-size: 4rem;
                }
            }
        }
        footer {
            height: 15%;
            width: 95%;
            background-color: $footer_bg_color;
            border-radius: 1rem;
        }
    }
}